
.scrub-backdrop {
    z-index: 9999999 !important;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
}
.zpore-control-header {
.scrub-bar {
    height: 6px;
    border-radius: 3px;
    margin-left: 55px;
    position: relative;
    background-color: #58585826;
    cursor: pointer;
    .inner-playbar {
        border-radius: 6px;
        background-color: #e4e4e4c9;
        height: 6px;
    }

    .playhead {
        height: 8px;
        background-color: blue;
        width: 15px !important;
        transform: translate(-10px, -1px);
    }
}

.scrub-bar:active {
    .playhead {
        transform: translate(-10px, -1px) scale(1.5);
        animation-duration: 0.5s;
        animation-name: grow-playhead;
    }
}
}

@keyframes grow-playhead {
    0% {
        transform: translate(-10px, -1px) scale(1);
    }
    100% {
        transform: translate(-10px, -1px) scale(1.5);
    }
}
