.player-animation {
    width: 130px;
    height: 130px;
    position: relative;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z: 300;
    background-color: transparent;
    backdrop-filter: blur(8px);
    //background-color: rgba(122, 116, 116, 0.2392156863);
    border-radius: 30px;

    overflow: hidden;
    canvas {
        border-radius: 15px;
    }
    
    svg {
        width: 130px;
        height: 130px;

        path, rect {
            opacity: 0.4;
        }
        
        rect, path, circle {
            transform: translate(15px, 15px);
            transform-origin: center;
        }
        path {
            stroke: transparent;
            fill: url(#linear-gradient);
        }
    }
    img.player-zorb {
        transform: translate(-16px, -16px);
    }

    img.player-outline {
        width: 100px;
        height: 100px;
    }
}
