.remix-details {
    .logo-small {
        //transform: rotate(90deg);
        position: absolute;
        top: 25px;
        right: 25px;
        width: 100px;
        object-fit: fill;
    }
}
.remix-dna-details {
    box-shadow: 0px 0px 13px 5px rgb(0 0 0 / 35%);
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    align-items: flex-start;
    background-color: black;
    overflow: hidden;
    border-radius: 10px;
    border: 2px solid #393737b3;
    border-radius: 15px;
    width: 260px;
    height: 260px;
    margin-top: -5px;
    
    .contract-container {
        padding: 8px;
        background-color: #7a74743d;
        border-radius: 100px;
        //overflow: hidden;
        margin: 3px;
        border: 1px solid #ffffff05;
        align-content: flex-start;
        align-items: flex-start;
        max-width: 80px;

        .drop-name {
            font-size: 8px;
            color: white;
            //white-space: nowrap;
            left: 0px;
            max-width: 70px;
        }

        .remix-atom {
            margin: 2px;
            filter: brightness(2);
            background-image: linear-gradient(45deg, black -10px, transparent);
        }
    }
    .remix-bpm {
        background-color: #7a74743d;
    }
}
